.go-next-btn {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 20px;
  margin: auto;
  height: 40px;
  width: 30px;
  cursor: pointer;
  animation: go-next-btn 1s alternate infinite;
  transition: all 1s;
  z-index: 999;
  
  .gn-top {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 15px;
    height: 15px;
    border-left: 2px solid transparent;
    border-top: 2px solid transparent;
    border-right: 2px solid rgba(255, 255, 255, 0.507);
    border-bottom: 2px solid rgba(255, 255, 255, 0.507);
    transform: rotate(45deg);
  }
  .gn-bottom {
    position: absolute;
    top: 7px;
    left: 0;
    right: 0;
    margin: auto;
    width: 15px;
    height: 15px;
    border-left: 2px solid transparent;
    border-top: 2px solid transparent;
    border-right: 2px solid rgba(255, 255, 255, 0.507);
    border-bottom: 2px solid rgba(255, 255, 255, 0.507);
    transform: rotate(45deg);
  }
}
@keyframes go-next-btn
{
  from {
    bottom: 20px;
  }
  to {
    bottom: 10px;
  }
}
// Home Page
.home {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  overflow: hidden;
  &-bg {
    height: 100%;
    background: url(../static/x4gwqn.jpg);
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    filter: blur(0px);
    transition: all .3s;
    &-title {
      box-sizing: border-box;
      width: 100%;
      height: 18vh;
      position: absolute;
      top: 0;
      bottom: 0;
      font-size: 6vh;
      letter-spacing: 10px;
      line-height: 18vh;
      text-align: center;
      margin: auto;
      background: rgba(204, 204, 204, 0.562);
      color: #fff;
      border-top: 2px solid rgba(255, 255, 255, 0.349);
      border-bottom: 2px solid rgba(255, 255, 255, 0.349);
    }
  }
  &-bg-blur {
    filter: blur(5px);
  }
  &-main {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}